import { vec4, mat4 } from "gl-matrix";
import * as WebGLUtils from "%COMMON/WebGLUtils";
/**
 * This class encapsulates the "view", where all of our WebGL code resides. This class, for now, also stores all the relevant data that is used to draw. This can be replaced with a more formal Model-View-Controller architecture with a bigger application.
 */

export class View {
    //the webgl rendering context. All WebGL functions will be called on this object
    private gl: WebGLRenderingContext;
    //an object that represents a WebGL shader
    private shaderProgram: WebGLShader;

    //The number of vertices in our drawing. We need this information when drawing
    private numVertices: number;
    //a projection matrix, that encapsulates how what we draw corresponds to what is seen
    private proj: mat4;

    constructor(gl: WebGLRenderingContext) {
        this.gl = gl;
    }

    /**
     * This function sets up everything needed to draw. This includes creating the model, sending model data to shader memory and setting up the shaders.
     * @param vShaderSource the source of the vertex shader, as a string
     * @param fShaderSource the source of the fragment shader, as a string
     */
    public init(vShaderSource: string, fShaderSource: string): void {
        //create and set up the shader
        this.shaderProgram = WebGLUtils.createShaderProgram(this.gl, vShaderSource, fShaderSource);

        //create the data for our quad. The data is simply the (x,y) coordinates of each vertex of the quad in counter-clockwise order.
        let vertexData: Float32Array = new Float32Array([-100, -100, 100, -100, 100, 100, -100, -100, 100, 100, -100, 100]);

        //since there are two numbers per vertex, the number of vertices is half the length of the above array
        this.numVertices = vertexData.length / 2;

        //enable the current program
        this.gl.useProgram(this.shaderProgram);


        //A vertex buffer object used to store the vertex data for rendering
        let vbo: WebGLBuffer;
        //create a vertex buffer object
        vbo = this.gl.createBuffer();
        //bind the buffer to GL_ARRAY_BUFFER
        this.gl.bindBuffer(this.gl.ARRAY_BUFFER, vbo);
        //copy over the vertex data
        this.gl.bufferData(this.gl.ARRAY_BUFFER, vertexData, this.gl.STATIC_DRAW);

        //get the location of the vPosition attribute in the shader program
        let positionLocation: number = this.gl.getAttribLocation(this.shaderProgram, "vPosition");

        //tell webgl that the position attribute can be found as 2-floats-per-vertex with no gaps
        this.gl.vertexAttribPointer(positionLocation, 2, this.gl.FLOAT, false, 0, 0);
        //tell webgl to enable this vertex attribute array, so that when it draws it will use this

        //Only one VBO is enabled at a time. So when a single program uses multiple models, one must bind the appropriate buffer before drawing it. If the data organization changes per model, then the line above and below must also be repeated, because they apply to the currently bound buffer. 

        //In this program there is only one model. So we can do all of this only once, here in the init
        this.gl.enableVertexAttribArray(positionLocation);


        //set the clear color
        this.gl.clearColor(1, 1, 0, 1);


        //Our quad is in the range (-100,100) in X and Y, in the "virtual world" that we are drawing. We must specify what part of this virtual world must be drawn. We do this via a projection matrix, set up as below. In this case, we are going to render the part of the virtual world that is inside a square from (-200,-200) to (200,200). Since we are drawing only 2D, the last two arguments are not useful. The default Z-value chosen is 0, which means we only specify the last two numbers such that 0 is within their range (in this case we have specified them as (-100,100))
        this.proj = mat4.ortho(mat4.create(), -200, 200, -200, 200, -100, 100);

        //We must also specify "where" the above part of the virtual world will be shown on the actual canvas on screen. This part of the screen where the above drawing gets pasted is called the "viewport", which we set here. The origin of the viewport is left,bottom. In this case we want it to span the entire canvas, so we start at (0,0) with a width and height of 400 each (matching the dimensions of the canvas specified in HTML)
        this.gl.viewport(0, 0, 400, 400);

    }

    /**
     * The main drawing function in the view. This function must be called *every time* the screen * is to be refreshed.
     */
    public draw(): void {
        //clear the window
        this.gl.clear(this.gl.COLOR_BUFFER_BIT);

        //this is the color of the quad we draw. All vertices will get the same color
        let color: vec4 = vec4.create();
        color[0] = 1;
        color[1] = 0;
        color[2] = 1;
        this.gl.useProgram(this.shaderProgram);

        //get the location of the color in the shader
        let colorLocation: WebGLUniformLocation = this.gl.getUniformLocation(this.shaderProgram, "vColor");
        //map the above color to this location
        this.gl.uniform4fv(colorLocation, color);

        //get the location of the projection matrix in the shader
        let projectionLocation: WebGLUniformLocation = this.gl.getUniformLocation(this.shaderProgram, "proj");
        //map the projection matrix to this location
        this.gl.uniformMatrix4fv(projectionLocation, false, this.proj);

        //the command below draws the quad. Specifically, it will use vertex data from whichever VBO is currently bound (in this program there is only one, enabled in init).

        //this.gl.TRIANGLES => read the vertices 3 at a time, and create a triangle out of them. In this way we will draw the quad using 2 triangles.
        //this.numVertices => read these many vertices. Usually they must match the vertices specified in the VBO, but we can use a lesser number to draw only a part of the model. However since we are using this.gl.TRIANGLES, we should ensure that the number of specified vertices is a multiple of 3. If it is not, the "incomplete" triangle will simply be ignored.
        this.gl.drawArrays(this.gl.TRIANGLES, 0, this.numVertices);

    }


}